.form {
  display: flex;
  flex-direction: column;
}

.calendar {
  width: 30px;
}

.description {
  border: 2px solid #ffffff;
  border-width: 2px 2px 0 2px;
  border-radius: 20px 20px 0 0;
  height: 44px;
  width: 280px;
  outline: none;
  color: #52555F;
  
  padding-left: 10px;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.02em;
}

.description:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 50px #f5f6fb !important; /* Цвет фона */
  -webkit-text-fill-color: #c7ccdc !important; /* цвет текста */
  color: #c7ccdc !important; /* цвет текста */
}

.input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 50px #f5f6fb !important; /* Цвет фона */
}

.description::placeholder {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.02em;
  color: #c7ccdc !important;
}

.arrow {
  color: #c7ccdc;
}

.inputWrap {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 80px;
  justify-content: center;
  align-items: center;
  margin-right: 32px;
}

.input {
  padding: 10px 16px 10px 19px;
  border-radius: 22px 0 0 22px;

  border: 2px solid #ffffff;
  width: 150px;

  outline: none;
  text-align: right;
  height: 20px;
  font-size: 12px;
  font-weight: bold;
  height: 44px;
}

.input::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: black;
  opacity: 1; /* Firefox */
  font-weight: bold;
  font-size: 12px;
}



.buttonWrap {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.buttonInput {
  width: 130px;
  height: 44px;
  border-radius: 16px;
  /* box-shadow: 1px 3px 5px rgba(255, 107, 8, 0.35); */
 
  color: #ff751d;;
  margin-right: 20px;
  border: 2px solid #f5f6fb;
  
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
      background: linear-gradient(0deg, #ddd, #fff);
      transition: .5s;
        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
            border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
            color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.buttonInput:hover {
  background-color: #ff751d;
  border-color: #ff751d;
  color: #ff751d;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  text-decoration: none;
}

.buttonClear {
  width: 130px;
  height: 44px;
  border-radius: 16px;
  background-color: #f5f6fb;
    border: 2px solid #f5f6fb;
/* box-shadow: 1px 2px 5px rgba(170, 178, 197, 0.4); */
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
      background: linear-gradient(0deg, #ddd, #fff);
      transition: .5s;
        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
            border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
            color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.buttonClear:hover{
    background-color: #ff751d;
      border-color: #ff751d;
      color: #ff751d;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
      text-decoration: none;
}

.calculateWrap {
  position: absolute;
  top: 12px;
  right: 10px;
}

.calculate {
  width: 20px;
  height: 20px;
}

.currencyWrapp {
  position: relative;
  display: flex;
  flex-direction: row;
}


@media screen and (min-width: 768px) {

    .exitBtn {
        display: none;
      }


  .calculateWrap {
    position: absolute;
    top: 12px;
    right: 10px;
  }

  .form {
    margin-bottom: 60px;
  }

  .select {
    width: 169px;
    height: 44px;
    margin-bottom: 0;
  }

  .description {
    background-color: #ffffff;
    border: 2px solid #f5f6fb;
    border-width: 2px 0 2px 2px;
    border-radius: 20px 0 0 0;
    width: 184px;
    padding-left: 20px;
   
  }

  .description::placeholder {
    padding: 10px;
    color: #c7ccdc !important;
  }

  .description:active {
    border-color: #f5f6fb;
    outline: none;
  }

  .input {
    padding: 10px 30px 10px 10px;
    width: 119px;
    outline: none;
    text-align: right;
    font-size: 12px;
    font-weight: bold;
    height: 44px;
    border: 2px solid #f6f7fc;
    border-radius: 0px 16px 16px 0px;
    background-color: #ffffff;
    color: #52555F;

    padding-right: 30px;
  }

  .input::placeholder {
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
    color: #c7ccdc;
  }

  .inputWrap {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;
  }

  .buttonWrap {
    align-items: center;
  }

  .description:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #fff !important; /* Цвет фона */
  }
}

@media screen and (min-width: 1280px) {
  .form {
    margin-bottom: 48px;
    flex-direction: row;
  }

  .buttonWrap {
    align-items: flex-start;
  }

  .description {
    margin-left: 0;
    width: 290px;
  }
}

@media screen and (max-width: 767px) {
  .description {
    width: 280px;
    margin-top: 20px;
    margin-left: 30px;
  }
    .currencyWrapp {
      margin-top: 32px;
    }

  .select {
    width: 250px;
  }
  .calculateWrap {
    position: static;

    margin: 0;
    width: 60px;
    height: 44px;
    border: 2px solid #f5f6fb;
    border-radius: 0px 22px 22px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
/* =============Form================= */
.notificationWraps{
  position: relative;
}
.errorNotification{
  position: absolute;
  left:50%;
  bottom: -10px;
  transform: translateX(-50%);
  font-size: 12px;
  color: red;
  width: fit-content;
  background-color: #fff;
  border-radius: 5px;
  padding: 5px 10px;
  box-shadow: 5px 10px 20px rgba(170, 178, 197, 0.4);
  white-space: nowrap;
  z-index: 1;
}
